// –°–∫—Ä—ã—Ç—ã–µ –ø–æ–ª—è –Ω–µ —Å–±—Ä–∞—Å—ã–≤–∞—é—Ç –∑–Ω–∞—á–µ–Ω–∏–µ –ø—Ä–∏ –≤—ã–∑–æ–≤–µ form.reset(),
// –¥–µ–ª–∞–µ–º –æ—á–∏—Å—Ç–∫—É —Ñ–æ—Ä–º—ã –≤ –æ—Ç–¥–µ–ª—å–Ω–æ–π —Ñ—É–Ω–∫—Ü–∏–∏
export function resetForm(form) {
  form.reset();

  // –ù–∞–π–¥—ë–º –≤—Å–µ —Å–∫—Ä—ã—Ç—ã–µ –ø–æ–ª—è –≤ —Ñ–æ—Ä–º–µ –∏ —Å–±—Ä–æ—Å–∏–º –∏—Ö –∑–Ω–∞—á–µ–Ω–∏–µ
  [...form.querySelectorAll('[type="hidden"]')].forEach((input) => {
    input.value = '';
  });
}
